import React, { useState } from 'react'
import axios from 'axios'
import { navigate } from 'gatsby'
import styled from 'styled-components'
import { Card, CardHeader, CardBody, CardFooter } from '../components/Card'
import { Button } from '../components/Button'
import { handleLogin, isLoggedIn } from '../services/auth'

const LoginButton = styled(Button)`
    background-color: #fff;
`

const FormControl = styled.div`
    margin: 1rem;
    & > label {
        display: block;
        font-size: 75%;
        font-weight: bold;
    }
    & > input {
        width: 100%;
        display: block;
        padding: 0.25rem;
        background-color: #fff;
        border: 1px solid var(--color-grey);
        border-radius: 0.25rem;
        outline: none;
        transition: border 250ms;
        &:focus {
            border: 1px solid var(--color-black);
        }
    }
`

const LoginFormCard = styled(Card)`
    margin-top: 3rem;
    max-width: 400px;
    width: 85%;
`

const LoginPage = () => {
    const [username, setUsername] = useState('user')
    const [password, setPassword] = useState('password')

    const handleChangeUsername = event => setUsername(event.target.value)
    const handleChangePassword = event => setPassword(event.target.value)
    const handleSubmit = event => {
        event.preventDefault()
        axios.get('http://localhost:3030/auth/login')
            .then(response => {
                console.table(response.data)
                axios.get('http://localhost:3030/auth/lookup', response.data)
                    .then(response => {
                        console.table(response.data)
                        handleLogin(response.data)
                    })
                    .catch(error => console.error(error))
            })
            .catch(error => console.error(error))
    }

    if (isLoggedIn()) {
        navigate('/')
    }

    return (
        <LoginFormCard>
            <CardHeader>
                Login Form
            </CardHeader>
            <CardBody>
            <form method="post"
                onSubmit={ event => {
                    handleSubmit(event)
                    navigate('/')
                }}
            >
                <FormControl>
                    <label htmlFor="email-field">Emaail</label>
                    <input id="email-field" onChange={ handleChangeUsername } type="text" value={ username } />
                </FormControl>

                <FormControl>
                    <label htmlFor="pasword-field">Password</label>
                    <input id="password-field" onChange={ handleChangePassword } type="password" value={ password } />         
                </FormControl>

                <FormControl>
                    <LoginButton>
                        Login
                    </LoginButton>
                </FormControl>
            </form>

            </CardBody>
            <CardFooter>
                Having trouble?
            </CardFooter>
        </LoginFormCard>
    )
}

export default LoginPage
